#home-page{
    height: 100vh;
    width: 100vw;
    background: linear-gradient(329.54deg, #7ec9de 0%, #39cbcb 100%);
    display: flex;
    justify-content: center;
    align-items: center;
}

#home-page .content-wrapper main{
    max-width: 350px;
}
#home-page .content-wrapper main h1{
    font-size: 76px;
    font-weight: 900;
    line-height: 70px;
}
#home-page .content-wrapper main p{
   margin-top: 40px;
   font-size: 24px;
   line-height: 34px;
}
#home-page .content-wrapper{
    position: relative;
    width: 100%;
    max-width: 1050px;
    height: 100%;
    max-height: 680px;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: space-between;
    background: url('../../images/Ilustra02.svg') no-repeat 80% center;
}
.content-wrapper .location{
    position: absolute;
    right: 0;
    top: 0;
    font-size: 24px;
    line-height: 34px;
    display: flex;
    flex-direction: column;
    text-align: right;
}
.content-wrapper .location strong{
    font: 800;
}
.content-wrapper .enter-app{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 80px;
    height: 80px;
    background: #ffd666;
    border-radius: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.2s;
}
.content-wrapper .enter-app:hover{
    background: #96feff;
}
